import React from "react";

import SpecialityLetter from "./SpecialistLetter";
import EmergencyDptLetter from "./EmergenceyDptLetter";

export default function ReferralModule({
  slotDescription,
  aptData,
  setAptData,
  setInputFieldValues,
  inputFieldValues,
}) {
  return (
    <>
      <SpecialityLetter
        slotDescription={slotDescription}
        aptData={aptData}
        setAptData={setAptData}
        setInputFieldValues={setInputFieldValues}
        inputFieldValues={inputFieldValues}
      />
      <hr style={{ color: "#CCCCCC", border: "1px solid" }} />
      <EmergencyDptLetter
        slotDescription={slotDescription}
        aptData={aptData}
        setAptData={setAptData}
        setInputFieldValues={setInputFieldValues}
        inputFieldValues={inputFieldValues}
      />
    </>
  );
}
