import React, { useRef, useState } from "react";
import {
  Accordion,
  Button,
  Card,
  Col,
  Dropdown,
  DropdownButton,
  Form,
  Row,
} from "react-bootstrap";
import { BsThreeDotsVertical } from "react-icons/bs";
import paginationFactory from "react-bootstrap-table2-paginator";
import IMAGES from "../../../assets/images";
import GenericPatientCard from "../../../components/Theme/GenericPatientCard";
import { IoIosArrowForward } from "react-icons/io";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useParams } from "react-router-dom";
import { getPatient } from "../../../app/features/patient/patient.slice";
import { getPatientAppointmentNotesDetail } from "../../../app/features/appointment/appointment.slice";
import BootstrapTable from "react-bootstrap-table-next";
import moment from "moment";
import { useForm } from "react-hook-form";
import {
  createPatientNotes,
  updatePatientNotes,
} from "../../../app/features/patientNotes/patientNotes.slice";
import { FaEdit } from "react-icons/fa";
import PrintComponents from "react-print-components";
import ReferralModule from "./Accordion/Referral";
import MedicalCertificateModule from "./Accordion/MedicalCertificate";
import { PiDownloadSimpleBold } from "react-icons/pi";
import { getUser } from "../../../app/features/userManagement/user.slice";
import PrescriptionPDF from "../../../components/Theme/PrescriptionPDF";
import { PATH } from "../../../utils/path";

export default function PatientAppointmentNotesDetails() {
  const [editing, setEditing] = useState(false);
  const printButtonRef = useRef(null);

  const { register, handleSubmit, reset } = useForm();

  const param = useParams();
  const dispatch = useDispatch();
  const currentDate = new Date();
  const formattedDate = currentDate.toISOString().slice(0, 19);

  const { getPatientData } = useSelector((state) => state.patient);
  const { patientAppointmentNotes } = useSelector((state) => state.appointment);

  const user = JSON.parse(localStorage.getItem("family_doc_app"));

  function rowStyle() {
    return { backgroundColor: "#F8F8F8" };
  }
  const columns = [
    {
      dataField: "medicineName",
      text: "Medicine Name",
      headerAttrs: {
        hidden: true,
      },
      formatter: (row, cell) => (
        <>
          <p className="color-99 mb-0">Medicine Name</p>
          <p className="table-custom-data">
            {cell?.medicineName ? cell?.medicineName : "N/A"}
          </p>
        </>
      ),
    },
    {
      dataField: "doseFrequency",
      text: "Dose Frequency",
      headerAttrs: {
        hidden: true,
      },
      formatter: (row, cell) => (
        <>
          <p className="color-99 mb-0">Dose Frequency</p>
          <p className="table-custom-data">
            {cell?.doseFrequency ? cell?.doseFrequency : "N/A"}
          </p>
        </>
      ),
    },
    {
      dataField: "dose",
      text: "Dose",
      headerAttrs: {
        hidden: true,
      },
      formatter: (row, cell) => (
        <>
          <p className="color-99 mb-0">Dose</p>
          <p className="table-custom-data">{cell?.dose ? cell?.dose : "N/A"}</p>
        </>
      ),
    },
    {
      dataField: "formulation",
      text: "Formulation",
      headerAttrs: {
        hidden: true,
      },
      formatter: (row, cell) => (
        <>
          <p className="color-99 mb-0">Formulation</p>
          <p className="table-custom-data">
            {cell?.formulation ? cell?.formulation : "N/A"}
          </p>
        </>
      ),
    },
    {
      dataField: "quantity",
      text: "Quantity",
      headerAttrs: {
        hidden: true,
      },
      formatter: (row, cell) => (
        <>
          <p className="color-99 mb-0">Quantity</p>
          <p className="table-custom-data">
            {cell?.quantity ? cell?.quantity : "N/A"}
          </p>
        </>
      ),
    },
  ];

  const customTotal = (from, to, size) => (
    <span className="react-bootstrap-table-pagination-total">
      <b> {from} </b> to <b>{to}</b> out of <b>{size} entries</b>
    </span>
  );

  const paginateOptions = {
    paginationSize: 2,
    pageStartIndex: 1,
    alwaysShowAllBtns: true,
    withFirstAndLast: false,
    hideSizePerPage: true,
    showTotal: true,
    paginationTotalRenderer: customTotal,
    disablePageTitle: true,
    sizePerPageList: [
      {
        text: "2",
        value: 2,
      },
    ],
  };

  useEffect(() => {
    const finalData = {
      patientId: param?.patientId,
    };
    dispatch(getPatient(finalData));
  }, [dispatch, param?.patientId]);

  useEffect(() => {
    const finalData = {
      userId: user?.userId,
    };
    dispatch(getUser(finalData));
  }, [dispatch, user?.userId]);

  useEffect(() => {
    const finalData = {
      appointmentId: param?.appointmentId,
    };
    dispatch(getPatientAppointmentNotesDetail(finalData));
  }, [dispatch, param?.appointmentId]);

  const moveToNext = () => {
    const finalData = {
      appointmentId: param?.appointmentId,
    };
    dispatch(getPatientAppointmentNotesDetail(finalData));
    setEditing(false);
    reset();
  };

  function getDayName(
    date = new Date(patientAppointmentNotes?.appointentDate),
    locale = "en-US"
  ) {
    return date?.toLocaleDateString(locale, { weekday: "short" });
  }
  const dayName = getDayName();

  function patientRefNotes(submittedData) {
    const finalData = {
      noteId: patientAppointmentNotes?.note?.noteId
        ? patientAppointmentNotes?.note?.noteId
        : 0,
      patientId: param?.patientId
        ? param?.patientId
        : patientAppointmentNotes?.note?.patientId,
      doctorId: user?.userId,
      appointmentId: param?.appointmentId,
      noteTypeId: 1,
      note: submittedData?.note,
      date: formattedDate,
      showNote: true,
      appointmentDetail: patientAppointmentNotes?.note
        ? {
            appointmentDetailId: 0,
          }
        : null,
    };
    if (patientAppointmentNotes?.note?.noteId) {
      dispatch(updatePatientNotes({ finalData, moveToNext }));
    } else {
      dispatch(createPatientNotes({ finalData, moveToNext }));
    }
  }

  const dateString = patientAppointmentNotes?.startTime;
  const utcTime = new Date(dateString);

  // Get local time offset in minutes
  const offsetInMinutes = utcTime.getTimezoneOffset();
  // Convert to local time by subtracting the offset
  const localTime = new Date(utcTime.getTime() - offsetInMinutes * 60000);
  // Format the local time as a string (HH:mm:ss format)
  const formattedLocalTime = localTime.toLocaleTimeString([], {
    hour: "2-digit",
    minute: "2-digit",
  });

  return (
    <>
      <nav aria-label="breadcrumb">
        <ol className="breadcrumb">
          <li className="breadcrumb-item">
          <Link
              to={PATH.PATIENTS}
              className="text-decoration-none fs-5"
              style={{ color: "#999999" }}
            >
              Patients
            </Link>
          </li>
          <IoIosArrowForward
            style={{ color: "#999999" }}
            className="ms-2 mt-2"
          />
          <li
            className="breadcrumb-item active fs-5"
            aria-current="page"
            style={{ color: "#999999" }}
          >
            {`${getPatientData?.firstName} ${getPatientData?.lastName}`}
          </li>
          <IoIosArrowForward
            style={{ color: "#999999" }}
            className="ms-2 mt-2"
          />
          <li
            className="breadcrumb-item active fs-5"
            aria-current="page"
            style={{ color: "#999999" }}
          >
            Appointments
          </li>
          <IoIosArrowForward
            style={{ color: "#999999" }}
            className="ms-2 mt-2"
          />
          <li
            className="breadcrumb-item active fs-5"
            aria-current="page"
            style={{ color: "#000071" }}
          >
            Appointment Notes
          </li>
        </ol>
      </nav>
      <Row className="patient-appointment-notes-details">
        <Col xl={3} lg={4} md={4}>
          <div className="shadow-sm">
            <GenericPatientCard
              name={
                getPatientData?.firstName
                  ? `${getPatientData?.firstName} ${getPatientData?.lastName}`
                  : "N/A"
              }
              genderInfo="true"
              gender={getPatientData?.gender ? getPatientData?.gender : "N/A"}
              dobInfo="true"
              dob={
                getPatientData?.dob ? getPatientData?.dob.split("T")[0] : "N/A"
              }
              locationInfo="true"
              location={
                getPatientData?.currentAddress
                  ? getPatientData?.currentAddress
                  : "N/A"
              }
              currentLocationInfo="true"
              currentLocation={
                getPatientData?.currentAddress
                  ? getPatientData?.currentAddress
                  : "N/A"
              }
              patientLocalGPInfo="true"
              patientLocalGP={
                getPatientData?.patientLocalGP
                  ? getPatientData?.patientLocalGP
                  : "N/A"
              }
              emailInfo="true"
              email={getPatientData?.email ? getPatientData?.email : "N/A"}
              pharmacyInfo="true"
              pharmacy="Clinix"
              phoneInfo="true"
              phone={
                getPatientData?.phoneNumber
                  ? getPatientData?.phoneNumber
                  : "N/A"
              }
              image={getPatientData?.imageUrl}
            />
          </div>
        </Col>
        <Col xl={9} lg={8} md={8}>
          <Card className="px-4 pt-4 shadow-sm patient-appointment-notes-details">
            <div className="py-3">
              <h4 className="px-2 py-2 header-border">Appointmnet Notes</h4>
            </div>

            <Accordion defaultActiveKey="0">
              <Accordion.Item eventKey="0" className="my-3">
                <Accordion.Header>
                  <h4>Appointment Details</h4>
                </Accordion.Header>
                <Accordion.Body className="py-4">
                  <>
                    <div className="d-flex justify-content-between py-3 pb-4">
                      <div className="d-flex">
                        <img
                          src={IMAGES.APPOINTMENT_ICON}
                          alt="Appointment Icon"
                          className="appointment-icon-size color-dk-blue"
                        />
                        <p className="ps-2 pt-1 mb-0 fw-bold align-items-center">
                          {/* Mon, 23 Jan 2023, 6:20pm */}
                          {dayName},{" "}
                          {moment(
                            patientAppointmentNotes?.appointentDate
                          ).format("DD-MMM-YYYY")}
                          , {formattedLocalTime}
                        </p>
                      </div>
                    </div>
                    <div>
                      <p style={{ color: "#999999" }} className="mb-1">
                        Presenting Complaint
                      </p>
                      <p>
                        {patientAppointmentNotes?.appointmentDetail
                          ?.presentingComplaint
                          ? patientAppointmentNotes?.appointmentDetail
                              ?.presentingComplaint
                          : "N/A"}
                      </p>
                    </div>
                    {/* <div>
                      <p style={{ color: "#999999" }} className="mb-1">
                        History of presenting complaint & examination
                      </p>
                      <p>
                        {patientAppointmentNotes?.appointmentDetail
                          ?.historyOfPresentingComplaint
                          ? patientAppointmentNotes?.appointmentDetail
                              ?.historyOfPresentingComplaint
                          : "N/A"}
                      </p>
                    </div> */}
                    <div>
                      <p style={{ color: "#999999" }} className="mb-1">
                        Past Medical History
                      </p>
                      <p>
                        {patientAppointmentNotes?.appointmentDetail
                          ?.pastMedicalHistory
                          ? patientAppointmentNotes?.appointmentDetail
                              ?.pastMedicalHistory
                          : "N/A"}
                      </p>
                    </div>
                    <div>
                      <p style={{ color: "#999999" }} className="mb-1">
                        Medications
                      </p>
                      <p>
                        {patientAppointmentNotes?.appointmentDetail?.medications
                          ? patientAppointmentNotes?.appointmentDetail
                              ?.medications
                          : "N/A"}
                      </p>
                    </div>
                    <div>
                      <p style={{ color: "#999999" }} className="mb-1">
                        Allergies
                      </p>
                      <p>
                        {patientAppointmentNotes?.appointmentDetail?.allergies
                          ? patientAppointmentNotes?.appointmentDetail
                              ?.allergies
                          : "N/A"}
                      </p>
                    </div>
                    <div>
                      <p style={{ color: "#999999" }} className="mb-1">
                        Family History
                      </p>
                      <p>
                        {patientAppointmentNotes?.appointmentDetail
                          ?.familyHistory
                          ? patientAppointmentNotes?.appointmentDetail
                              ?.familyHistory
                          : "N/A"}
                      </p>
                    </div>

                    <div>
                      <p style={{ color: "#999999" }} className="mb-1">
                        Social History
                      </p>
                      <p>
                        {patientAppointmentNotes?.appointmentDetail
                          ?.socialHistory
                          ? patientAppointmentNotes?.appointmentDetail
                              ?.socialHistory
                          : "N/A"}
                      </p>
                    </div>
                    <div>
                      <p style={{ color: "#999999" }} className="mb-1">
                        Suspected Diagnosis
                      </p>
                      <p>
                        {patientAppointmentNotes?.appointmentDetail
                          ?.suspectedDiagnosis
                          ? patientAppointmentNotes?.appointmentDetail
                              ?.suspectedDiagnosis
                          : "N/A"}
                      </p>
                    </div>
                    <div>
                      <p style={{ color: "#999999" }} className="mb-1">
                        Recommended Action
                      </p>
                      <p>
                        {patientAppointmentNotes?.appointmentDetail
                          ?.recommendedActions
                          ? patientAppointmentNotes?.appointmentDetail
                              ?.recommendedActions
                          : "N/A"}
                      </p>
                    </div>
                    <div>
                      <p style={{ color: "#999999" }} className="mb-1">
                        Sign off
                      </p>
                      <p>
                        {patientAppointmentNotes?.appointmentDetail?.signOff
                          ? patientAppointmentNotes?.appointmentDetail?.signOff
                          : "N/A"}
                      </p>
                    </div>
                  </>
                </Accordion.Body>
              </Accordion.Item>
            </Accordion>

            <Accordion>
              <Accordion.Item eventKey="0" className="my-3">
                <Accordion.Header>
                  <h4>Clinical Notes</h4>
                </Accordion.Header>
                <Accordion.Body
                  className={`mx-4 ${
                    !patientAppointmentNotes?.note?.note || editing === true
                      ? "mb-5"
                      : ""
                  }`}
                >
                  <Form onSubmit={handleSubmit(patientRefNotes)}>
                    <Form.Group
                      className="mb-3"
                      controlId="medicalForm.ControlTextarea1"
                    >
                      <span className="d-flex justify-content-between">
                        <Form.Control
                          as="textarea"
                          rows={3}
                          className={`${
                            (editing || patientAppointmentNotes?.note?.note) &&
                            "bg-transparent"
                          }`}
                          placeholder="Tell about Clinical Notes"
                          {...register("note", { required: true })}
                          defaultValue={
                            patientAppointmentNotes?.note?.note ?? ""
                          }
                          readOnly={
                            patientAppointmentNotes?.note?.note && editing
                              ? false
                              : patientAppointmentNotes?.note?.note
                              ? true
                              : false
                          }
                          maxLength={250}
                        />

                        {patientAppointmentNotes?.note?.note &&
                          editing === false && (
                            <DropdownButton
                              id="dropdown-item-button"
                              title={
                                <BsThreeDotsVertical
                                  className="float-right color-99"
                                  size={25}
                                />
                              }
                              size="sm"
                              bsPrefix="moreIcon"
                              className="moreIcon edit-dropdown"
                              direction="left"
                            >
                              <div className="set-dropdown-menu">
                                <Dropdown.Item
                                  // as="button"
                                  className="d-flex align-items-center"
                                  onClick={() => {
                                    setEditing(true);
                                    // setSave(true);
                                    // setEditData({
                                    //   data: getDetails?.createPatientRefNotes?.data,
                                    // });
                                  }}
                                >
                                  <FaEdit
                                    size={18}
                                    style={{ color: "#1A1A1A" }}
                                  />
                                  <span
                                    className="px-2"
                                    style={{
                                      fontSize: "14px",
                                      color: "#1A1A1A",
                                    }}
                                  >
                                    Edit
                                  </span>
                                </Dropdown.Item>
                                {/* <Dropdown.Item
                                // as="button"
                                className="d-flex align-items-center"
                                onClick={() => {
                                  setDeleteId(patientAppointmentNotes?.note?.noteId);
                                  setOpen(true);
                                }}
                              >
                                <RiDeleteBin5Fill
                                  size={18}
                                  style={{ color: "#FD2121" }}
                                />
                                <span
                                  className="px-2"
                                  style={{ fontSize: "14px", color: "#FD2121" }}
                                >
                                  Remove
                                </span>
                              </Dropdown.Item> */}
                              </div>
                            </DropdownButton>
                          )}
                      </span>
                    </Form.Group>

                    {(!patientAppointmentNotes?.note?.note ||
                      editing === true) && (
                      <Button
                        className="mb-3 start-apt-btn float-right"
                        type="submit"
                      >
                        Save
                      </Button>
                    )}
                  </Form>
                </Accordion.Body>
              </Accordion.Item>
            </Accordion>

            <Accordion>
              <Accordion.Item eventKey="0" className="my-3">
                <Accordion.Header>
                  <h4>Prescription</h4>
                </Accordion.Header>
                <Accordion.Body className="py-4">
                  <>
                    <Card className="bg-light px-3 mb-3">
                      <div className="custom-table-spacing">
                        {patientAppointmentNotes?.prescription?.medicineList
                          ?.length > 0 ? (
                          <>
                            <div className="d-flex justify-content-end pt-3">
                              <PrintComponents
                                trigger={
                                  <button
                                    ref={printButtonRef}
                                    className="border-0 px-3 py-2 rounded-2"
                                  >
                                    <PiDownloadSimpleBold size={20} /> Download
                                    PDF
                                  </button>
                                }
                              >
                                <PrescriptionPDF
                                  prescriptionData={
                                    patientAppointmentNotes?.prescription
                                  }
                                />
                              </PrintComponents>
                            </div>
                            <BootstrapTable
                              keyField="id"
                              data={
                                patientAppointmentNotes?.prescription
                                  ?.medicineList
                                  ? patientAppointmentNotes?.prescription
                                      ?.medicineList
                                  : []
                              }
                              columns={columns}
                              bordered={false}
                              rowStyle={rowStyle}
                              wrapperClasses="table-responsive"
                              pagination={paginationFactory(paginateOptions)}
                            />
                          </>
                        ) : (
                          <p className="text-black text-center w-100 mb-0 py-2">
                            No Prescription
                          </p>
                        )}
                      </div>
                    </Card>
                  </>
                </Accordion.Body>
              </Accordion.Item>
            </Accordion>

            <Accordion>
              <Accordion.Item eventKey="0" className="my-3">
                <Accordion.Header>
                  <h4>Referral</h4>
                </Accordion.Header>
                <Accordion.Body className="py-0">
                  <ReferralModule />
                </Accordion.Body>
              </Accordion.Item>
            </Accordion>

            <Accordion>
              <Accordion.Item eventKey="0" className="my-3">
                <Accordion.Header>
                  <h4 style={{color: '#44BC19'}}>Medical Certificate</h4>
                </Accordion.Header>
                <Accordion.Body>
                  <MedicalCertificateModule />
                </Accordion.Body>
              </Accordion.Item>
            </Accordion>
          </Card>
        </Col>
      </Row>
    </>
  );
}
