import { useSelector } from "react-redux";
import moment from "moment";

function PdfDownloadEmergency() {
  const { emergancyDepartmentLetter } = useSelector(
    (state) => state?.appointment?.patientAppointmentNotes || {}
  );
  const { detailDTO } = useSelector(
    (state) => state?.appointment?.patientAppointedDetail || {}
  );
  const { getUserData } = useSelector((state) => state.userRole);

  const currentDate = moment().format("DD/MM/YYYY");

  return (
    <div className="mb-5">
      <div className="text-center">
        <h1>FamilyDoc247</h1>
        <h6>General Practitioner (GP) / Family Physician</h6>
        <h6>Fitzwilliam Hall, Fitzwilliam Place, Dublin 2</h6>
        <h6>Tel: +353 1 906 9327</h6>
        <h6>Email : contact@familydoc247.ie</h6>
      </div>
      <p className="my-5">{currentDate}</p>
      <p className="mb-5">
        <span className="font-weight-600">To: </span>
        Emergency Department Doctor On Duty
      </p>
      <div>
        <p>
          Re: {emergancyDepartmentLetter?.patient},{" "}
          {detailDTO?.currentAddress ? detailDTO?.currentAddress : "N/A"}
        </p>
        <p>
          DOB:{" "}
          {moment(detailDTO?.dob ? detailDTO?.dob : "N/A").format("MM/DD/YYYY")}
          , Tel: {detailDTO?.phoneNumber ? detailDTO?.phoneNumber : "N/A"}
        </p>
      </div>
      <p className="mt-5">Dear Doctor On Duty,</p>
      <p>I would appreciate if you could see above-named patient.</p>
      <div className="mb-5">
        {emergancyDepartmentLetter?.referralNote && (
          <div>
            <p className="text-black-50 mb-0">Referral Notes</p>
            {emergancyDepartmentLetter?.referralNote}
          </div>
        )}
        {emergancyDepartmentLetter?.appointmentDetail && (
          <>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Presenting Complaint</p>
              {
                emergancyDepartmentLetter?.appointmentDetail
                  ?.presentingComplaint
              }
            </div>
            {/* <div className="mb-3">
              <p className="text-black-50 mb-0">
                History of presenting complaint
              </p>
              {
                emergancyDepartmentLetter?.appointmentDetail
                  ?.historyOfPresentingComplaint
              }
            </div> */}
            <div className="mb-3">
              <p className="text-black-50 mb-0">Past Medical History</p>
              {emergancyDepartmentLetter?.appointmentDetail?.pastMedicalHistory}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Medications</p>
              {emergancyDepartmentLetter?.appointmentDetail?.medications}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Allergies</p>
              {emergancyDepartmentLetter?.appointmentDetail?.allergies}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Family History</p>
              {emergancyDepartmentLetter?.appointmentDetail?.familyHistory}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Social History</p>
              {emergancyDepartmentLetter?.appointmentDetail?.socialHistory}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Suspected Diagnosis</p>
              {emergancyDepartmentLetter?.appointmentDetail?.suspectedDiagnosis}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Recommended Actions</p>
              {emergancyDepartmentLetter?.appointmentDetail?.recommendedActions}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Sign off</p>
              {emergancyDepartmentLetter?.appointmentDetail?.signOff}
            </div>
          </>
        )}
      </div>

      <p className="pb-3">
        I would appreciate your urgent assessment and management.
      </p>
      <p className="pb-2">Yours sincerely,</p>
      <p className="mb-0">Dr {getUserData?.name ? getUserData?.name : "N/A"}</p>
      <p>MCRN {getUserData?.mcrn ? getUserData?.mcrn : "N/A"}</p>
    </div>
  );
}

export default PdfDownloadEmergency;
