import { useSelector } from "react-redux";
import moment from "moment";

function PdfDownloadSpecialist() {
  const { specialistLetter } = useSelector(
    (state) => state?.appointment?.patientAppointmentNotes || {}
  );

  const { detailDTO } = useSelector(
    (state) => state?.appointment?.patientAppointedDetail || {}
  );
  const { getUserData } = useSelector((state) => state.userRole);

  const currentDate = moment().format("DD/MM/YYYY");

  return (
    <div className="mb-5">
      <div className="text-center">
        <h1>FamilyDoc247</h1>
        <h6>General Practitioner (GP) / Family Physician</h6>
        <h6>Fitzwilliam Hall, Fitzwilliam Place, Dublin 2</h6>
        <h6>Tel: +353 1 906 9327</h6>
        <h6>Email : contact@familydoc247.ie</h6>
      </div>
      <p className="my-5">{currentDate}</p>
      <p className="mb-5">
        <span className="font-weight-600">To: </span>
        {specialistLetter?.toDoctor}
      </p>
      <div>
        <p>
          Re: {specialistLetter?.patient ? specialistLetter?.patient : "N/A"},{" "}
          {detailDTO?.currentAddress ? detailDTO?.currentAddress : "N/A"}
        </p>
        <p>
          DOB:{" "}
          {moment(detailDTO?.dob ? detailDTO?.dob : "N/A").format("MM/DD/YYYY")}
          , Tel: {detailDTO?.phoneNumber ? detailDTO?.phoneNumber : "N/A"}
        </p>
      </div>
      <p className="mt-5">Dear Doctor,</p>
      <p>I would appreciate if you could see above-named patient.</p>
      <div className="mb-5">
        {specialistLetter?.referralNote && (
          <div>
            <p className="text-black-50 mb-0">Referral Notes</p>
            {specialistLetter?.referralNote}
          </div>
        )}
        {specialistLetter?.appointmentDetail && (
          <>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Presenting Complaint</p>
              {specialistLetter?.appointmentDetail?.presentingComplaint}
            </div>
            {/* <div className="mb-3">
              <p className="text-black-50 mb-0">
                History of presenting complaint
              </p>
              {
                specialistLetter?.appointmentDetail
                  ?.historyOfPresentingComplaint
              }
            </div> */}
            <div className="mb-3">
              <p className="text-black-50 mb-0">Past Medical History</p>
              {specialistLetter?.appointmentDetail?.pastMedicalHistory}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Medications</p>
              {specialistLetter?.appointmentDetail?.medications}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Allergies</p>
              {specialistLetter?.appointmentDetail?.allergies}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Family History</p>
              {specialistLetter?.appointmentDetail?.familyHistory}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Social History</p>
              {specialistLetter?.appointmentDetail?.socialHistory}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Suspected Diagnosis</p>
              {specialistLetter?.appointmentDetail?.suspectedDiagnosis}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Recommended Actions</p>
              {specialistLetter?.appointmentDetail?.recommendedActions}
            </div>
            <div className="mb-3">
              <p className="text-black-50 mb-0">Sign off</p>
              {specialistLetter?.appointmentDetail?.signOff}
            </div>
          </>
        )}
      </div>
      <p className="py-3">
        The above-named patient prefers to see you privately.
      </p>
      <p className="pb-3">
        I would appreciate your expert review and further management.
      </p>
      <p className="pb-2">Yours sincerely,</p>
      <p className="mb-0">
        Dr{" "}
        {getUserData?.name ? getUserData?.name : "N/A"}
      </p>
      <p>
        MCRN{" "}
        {getUserData?.mcrn ? getUserData?.mcrn : "N/A"}
      </p>
    </div>
  );
}

export default PdfDownloadSpecialist;
