import React, { useEffect, useRef, useState } from "react";
import {
  Button,
  Col,
  Dropdown,
  DropdownButton,
  Form,
  Row,
} from "react-bootstrap";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import {
  createMedCertificate,
  updateMedCertificate,
} from "../../../../../app/features/appointmentDetails/appointmentDetails.slice";
import { getPatientAppointmentNotesDetail } from "../../../../../app/features/appointment/appointment.slice";
import { BsThreeDotsVertical } from "react-icons/bs";
import { FaEdit } from "react-icons/fa";
import PrintComponents from "react-print-components";
import { PiDownloadSimpleBold } from "react-icons/pi";
import MedicalCertificatePDF from "../../../../../components/Theme/MedicalCertificatePDF";
import moment from "moment";

function MedicalCertificateModule() {
  const [editingMed, setEditingMed] = useState(false);
  const [showMedCertificate, setShowMedCertificate] = useState(false);
  const printButtonRef = useRef(null);
  const { register, handleSubmit, setValue } = useForm();
  const { patientAppointmentNotes } = useSelector((state) => state.appointment);

  const dispatch = useDispatch();
  const param = useParams();

  const { getPatientData } = useSelector((state) => state?.patient);
  const { getUserData } = useSelector((state) => state?.userRole);

  useEffect(() => {
    if (patientAppointmentNotes?.medicalCertificate?.unabletoAttendField) {
      setValue(
        "unabletoAttendField",
        patientAppointmentNotes?.medicalCertificate?.unabletoAttendField
      );
    }
  }, [patientAppointmentNotes, setValue]);

  const onCreateSuccess = () => {
    const finalData = {
      appointmentId: param?.appointmentId,
    };
    dispatch(getPatientAppointmentNotesDetail(finalData));
    setEditingMed(false);
  };

  function medicalCertificate(submittedData) {
    const finalData = {
      medicalCertificateId: patientAppointmentNotes?.medicalCertificate
        ?.medicalCertificateId
        ? patientAppointmentNotes?.medicalCertificate?.medicalCertificateId
        : 0,
      appointmentId: param?.appointmentId,
      name: submittedData.sufferingFrom,
      showMedicalCertificate: showMedCertificate,
      fromDate: submittedData?.fromDate,
      toDate: submittedData?.toDate,
      unabletoAttendField: submittedData.unabletoAttendField,
      pdfFile: null,
      sufferingFrom: submittedData?.sufferingFrom,
    };
    if (patientAppointmentNotes?.medicalCertificate?.sufferingFrom) {
      dispatch(updateMedCertificate({ finalData, onCreateSuccess }));
    } else {
      dispatch(createMedCertificate({ finalData, onCreateSuccess }));
    }
  }

  return (
    <div
      className={`mx-4 ${
        !patientAppointmentNotes?.medicalCertificate?.sufferingFrom ||
        editingMed === true
          ? "mb-5"
          : ""
      }`}
    >
      <Form
        onSubmit={handleSubmit(medicalCertificate)}
        className="med-certificate"
      >
        <Row>
          <Col md={12} className="d-flex justify-content-between">
            <Form.Check
              type="checkbox"
              id="show-medical-certificate"
              label="Show to Patient"
              onChange={() => {
                setShowMedCertificate(!showMedCertificate);
              }}
              disabled={
                (patientAppointmentNotes?.medicalCertificate
                  ?.showMedicalCertificate === true &&
                  editingMed) ||
                (patientAppointmentNotes?.medicalCertificate
                  ?.showMedicalCertificate === false &&
                  editingMed)
                  ? false
                  : patientAppointmentNotes?.medicalCertificate
                      ?.medicalCertificateId
                  ? true
                  : false
              }
              defaultChecked={
                patientAppointmentNotes?.medicalCertificate
                  ?.showMedicalCertificate
              }
            />

            {patientAppointmentNotes?.medicalCertificate?.sufferingFrom &&
              editingMed === false && (
                <DropdownButton
                  id="dropdown-item-med"
                  title={
                    <BsThreeDotsVertical
                      className="float-right color-99"
                      size={25}
                    />
                  }
                  size="sm"
                  bsPrefix="moreIcon"
                  className="moreIcon edit-dropdown"
                  direction="left"
                >
                  <div className="set-dropdown-menu">
                    <Dropdown.Item
                      // as="button"
                      className="d-flex align-items-center"
                      onClick={() => {
                        setEditingMed(true);
                      }}
                    >
                      <FaEdit size={18} style={{ color: "#1A1A1A" }} />
                      <span
                        className="px-2"
                        style={{
                          fontSize: "14px",
                          color: "#1A1A1A",
                        }}
                      >
                        Edit
                      </span>
                    </Dropdown.Item>
                    <Dropdown.Item className="d-flex align-items-center">
                      <PrintComponents
                        trigger={
                          <button
                            ref={printButtonRef}
                            className="bg-transparent border-0 p-0"
                          >
                            <PiDownloadSimpleBold size={20} /> Download PDF
                          </button>
                        }
                      >
                        <MedicalCertificatePDF
                          MedicalCertificateData={
                            patientAppointmentNotes?.medicalCertificate
                          }
                          getUserData={getUserData}
                          detailDTO={getPatientData}
                        />
                      </PrintComponents>
                    </Dropdown.Item>
                  </div>
                </DropdownButton>
              )}
          </Col>
          <Col lg={6} xs={12}>
            <Form.Group>
              <Form.Label>From</Form.Label>
              <Form.Control
                type="date"
                placeholderText="DD / MM / YY"
                className="custom-date"
                defaultValue={moment(
                  patientAppointmentNotes?.medicalCertificate?.fromDate
                ).format("DD-MM-YYYY")}
                disabled={
                  patientAppointmentNotes?.medicalCertificate?.fromDate &&
                  editingMed
                    ? false
                    : patientAppointmentNotes?.medicalCertificate?.fromDate
                    ? true
                    : false
                }
                name="fromDate"
                {...register("fromDate", { required: true })}
              />
            </Form.Group>
          </Col>
          <Col lg={6} xs={12}>
            <Form.Group controlId="">
              <Form.Label className="">To</Form.Label>
              <Form.Control
                type="date"
                placeholderText="DD / MM / YY"
                className="custom-date"
                defaultValue={moment(
                  patientAppointmentNotes?.medicalCertificate?.toDate
                ).format("DD-MM-YYYY")}
                disabled={
                  patientAppointmentNotes?.medicalCertificate?.toDate &&
                  editingMed
                    ? false
                    : patientAppointmentNotes?.medicalCertificate?.toDate
                    ? true
                    : false
                }
                name="toDate"
                {...register("toDate", { required: true })}
              />
            </Form.Group>
          </Col>
          <Col xs={12}>
            <Form.Group className="my-3" controlId="unable-to-attend">
              <Form.Label>Unable to Attend</Form.Label>
              <Form.Select
                aria-label="Select Unable to Attend"
                {...register("unabletoAttendField", { required: true })}
                disabled={
                  patientAppointmentNotes?.medicalCertificate
                    ?.unabletoAttendField && editingMed
                    ? false
                    : patientAppointmentNotes?.medicalCertificate
                        ?.unabletoAttendField
                    ? true
                    : false
                }
              >
                <option value="">Select Value</option>
                <option value="work">Work</option>
                <option value="school">School</option>
                <option value="college">College</option>
                <option value="jury duty">Jury Duty</option>
              </Form.Select>
            </Form.Group>
          </Col>
          <Col xs={12}>
            <Form.Group className="mb-3" controlId="medicalFormField">
              <Form.Label className="label-primary mt-2">
                Suffering Certificate
              </Form.Label>
              <span className="d-flex justify-content-between">
                <Form.Control
                  as="textarea"
                  rows={3}
                  className={`${
                    (editingMed ||
                      patientAppointmentNotes?.medicalCertificate
                        ?.sufferingFrom) &&
                    "bg-transparent"
                  }`}
                  placeholder="Tell about some medical certificate"
                  {...register("sufferingFrom", { required: true })}
                  defaultValue={
                    patientAppointmentNotes?.medicalCertificate
                      ?.sufferingFrom || ""
                  }
                  readOnly={
                    patientAppointmentNotes?.medicalCertificate
                      ?.sufferingFrom && editingMed
                      ? false
                      : patientAppointmentNotes?.medicalCertificate
                          ?.sufferingFrom
                      ? true
                      : false
                  }
                  maxLength={250}
                />
              </span>
            </Form.Group>
          </Col>
          <Col xs={3} className="w-100">
            {(!patientAppointmentNotes?.medicalCertificate?.sufferingFrom ||
              editingMed === true) && (
              <Button className="mb-3 start-apt-btn float-right" type="submit">
                Save
              </Button>
            )}
          </Col>
        </Row>
      </Form>
    </div>
  );
}

export default MedicalCertificateModule;
